import { ChangeDetectionStrategy, ChangeDetectorRef, Component, EventEmitter, Input, OnDestroy, OnInit, Output, TemplateRef } from '@angular/core';
import { CdkVirtualScrollViewport } from '@angular/cdk/scrolling';
import { CmacsSelectService } from './cmacs-select.service';
import { NzI18nService } from 'ng-zorro-antd/i18n';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { CmacsOptionComponent } from './cmacs-option.component';

@Component({
  selector: 'cmacs-option-container-virtual',
  templateUrl: './cmacs-option-container-virtual.component.html',
  styleUrls: ['./cmacs-option-container-virtual.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class CmacsOptionContainerVirtualComponent implements OnInit, OnDestroy {

  private destroy$ = new Subject();
  private scrolledIndex = 0;
  readonly itemSize = 32;
  readonly maxItemLength = 8;

  @Input() showSelectAll = true;
  @Input() showSearch = false;
  @Input() selectAllLabel = 'Select All';
  @Input() searchLabel = 'Search';
  @Input() showCmacsSearch = false;
  @Input() notFoundContentShow = true;
  @Input() notFoundContentCustom: TemplateRef<void>;
  @Input() nzMenuItemSelectedIcon: TemplateRef<void>;
  @Output() readonly nzScrollToBottom = new EventEmitter<void>();
  @Output() readonly onSearch = new EventEmitter<string>();

  constructor(
    public nzSelectService: CmacsSelectService,
    private i18n: NzI18nService,
    private cdr: ChangeDetectorRef
  ) { }

  ngOnInit() {
    this.setLocale();
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  onScrolledIndexChange(index: number): void {
    this.scrolledIndex = index;
    if (index === this.nzSelectService.listOfNzOptionComponent.length - this.maxItemLength) {
      this.nzScrollToBottom.emit();
    }
  }

  trackValue(_index: number, option: CmacsOptionComponent): any {
    return option.nzValue;
  }

  setLocale(): void {
    this.i18n.localeChange
      .pipe(takeUntil(this.destroy$))
      .subscribe(() => {
        switch (this.i18n.getLocale().locale) {
          case 'de':
            this.selectAllLabel = 'Alle auswählen';
            this.searchLabel = 'Suche';
            break;
          case 'en':
            this.selectAllLabel = 'Select All';
            this.searchLabel = 'Search';
            break;
          case 'ja':
            this.selectAllLabel = 'すべて選択';
            this.searchLabel = '検索';
            break;
          default:
            this.selectAllLabel = 'Select All';
            this.searchLabel = 'Search';
        }
        this.cdr.markForCheck();
      });
}

  isAllChecked(): number {
    const listOfNotDisabledItems = this.nzSelectService.listOfNzOptionComponent.filter(item => !item.nzDisabled);
    return this.nzSelectService.listOfCachedSelectedOption.length === listOfNotDisabledItems.length ?
      1 : !this.nzSelectService.listOfCachedSelectedOption.length ? 0 : -1;
  }

  updateCheckboxCache() {
    const isCheckedState = this.isAllChecked();

    if (!isCheckedState || isCheckedState === -1) {
      const listOfSelectedValue = [];
      this.nzSelectService.listOfFilteredOption.forEach(option => {
        if (!option.nzDisabled) {
          listOfSelectedValue.push(option.nzValue);
        }
      });
      this.nzSelectService.updateListOfSelectedValue(listOfSelectedValue, true);
    } else {
      this.nzSelectService.updateListOfSelectedValue([], true);
    }
  }
}
