import { AfterViewInit, Component, ElementRef, Input, OnChanges, OnInit, Renderer2 } from '@angular/core';
import { GridConfig } from '../core/interfaces/grid-config';
import { KPI } from '../cmacs-kpi/cmacs-kpi.component';

interface KPIPartialRow {
  key: string,
  name: string,
  date: string | null,
  children: KPIPartialRow[]
}

interface KPIPartial {
  total: number,
  part: number,
  partLabel: string,
  color: string,
  children: KPIPartialRow[]
}

@Component({
  selector: 'cmacs-kpi-partial',
  templateUrl: './cmacs-kpi-partial.component.html',
  styleUrls: ['./cmacs-kpi-partial.component.css']
})
export class CmacsKpiPartialComponent implements OnInit, OnChanges, AfterViewInit {

  // content
  @Input() view!: number[];
  @Input() data!: KPIPartial;
  @Input() columnsHeader: string[];

  // table
  dataTable!: any[];
  configuration!: GridConfig;

  scrollY = 200;
  scroll = { x: '434px', y: this.scrollY + 'px' };
  p = 1;
  loading = true;
  col2 = 150;
  col3 = 70;

  // chart
  minWidth = 434;
  colorsScheme!: string[];
  kpi!: KPI[];

  constructor(
    private renderer: Renderer2,
    private elementRef: ElementRef,
  ) { }

  ngOnInit(): void {
    this.fillKPI();
  }

  ngOnChanges(): void {
    this.setScroll();

    this.p = 1;
    if (this.view && this.view.length === 2) {
      this.p = this.view[0] > this.minWidth ? this.view[0] / this.minWidth : 1;
      if (this.renderer) {
        this.resize();
      }
    }

    setTimeout(() => {
      this.setConfiguration();
      this.setData();
    }, 0);
  }

  setScroll(): void {
    let p = 1;
    if (this.view && this.view.length === 2) {
      p = this.view[1] * 0.5 > this.scrollY ? this.view[1] * 0.5 / this.scrollY : 1;
    }
    this.scroll = { x: '300px', y: this.scrollY * p + 'px' };
  }

  resize() {
    const width = this.view[0];
    this.renderer.setStyle(this.elementRef.nativeElement, 'width', `${width}px`);
  }

  ngAfterViewInit(): void {
    this.resize();
  }

  fillKPI(): void {
    this.colorsScheme = ['#e6e6e6', this.data.color];
    this.kpi = [
      { count: this.data.total - this.data.part, description: '' },
      { count: this.data.part, description: '' }
    ];
  }

  setConfiguration(): void {
    this.configuration = {
      fields: [
        {
          celdType: 0,
          display: this.columnsHeader[0],
          property: 'name',
          editTemplate: 3,
          width: `${this.col2 * this.p}px`,
          minWidth: this.col2 * this.p - 10,
          maxWidth: this.col2 * this.p + 100,
          resizable: true
        },
        {
          celdType: 0,
          display: this.columnsHeader[1],
          property: 'date',
          width: `${this.col3 * this.p}px`,
          minWidth: this.col3 * this.p - 10,
          editTemplate: 2,
          editable: false
        }
      ],
      fieldId: 'key',
      fieldRate: 'fav'
    }
  }

  setData() {
    this.loading = true;
    this.dataTable = [];
    this.data.children.forEach(x => {
      this.dataTable.push(this.getItemTable(x));
    })
    setTimeout(() => {
      this.loading = false;
    }, 0);
  }

  getItemTable(x: KPIPartialRow): any {
    return {
      key: x.key,
      name: x.name,
      date: x.date,
      children: x.children.length > 0 ? x.children.map(c => this.getItemTable(c)) : undefined
    }
  }
}
